.scroll-wrapper {
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
}

.btn_scroll {
  display: block;
  font-weight: 600;
  padding: 5px 5px;
  text-decoration: none;
  border-radius: 10px;
  border-width: 1px;
  border-style: solid;
  border-color: rgb(0 0 0 / 29%);
  background-color: rgba(255, 255, 255, 1);
  margin-bottom: 10px;
  color: rgba(22, 24, 35, 0.75);
  box-shadow: 0 2px 8px rgb(0 0 0 / 6%);
}
.btn_scroll-icon {
  width: 32px;
  height: 32px;
  cursor: pointer;
  padding: 6px;
  display: flex;

  border-radius: 50%;
  text-align: center;
  background-color: red;
  transform: translateX(150%);
  font-size: 1.4rem;
  color: white;

  display: none;
}
.btn_scroll-icon__symbol {
  margin: auto;
  transform: translateY(25%);
}
