$color: rgba(254, 44, 85, 1);
@import "../Variable/variable";

.video-wrapper {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;

  &:first-child {
    padding: 60px 0 0 0;
  }

  // height: 80%;

  // margin-bottom: 20px;
}
.video_info_wrapper {
  display: flex;

  margin: 20px 10px 10px 10px;
  justify-content: center;
}
.video_info_user {
  display: flex;
  flex-direction: column;
  margin: 0 5px;
}
.avatar-info {
  height: 50px;
  width: 50px;
  border-radius: 30px;
  flex-shrink: 0;
  object-fit: cover;
}
.user_id {
  margin: 0 10px;
  font-weight: bolder;
  &:hover {
    cursor: pointer;
    text-decoration: underline;
  }
}
.user_name {
  margin: 0 5px;
  font-weight: lighter;
}
.video_info_tag {
  margin: 0 10px;
  width: 250px;
  display: flex;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
}
.tag_id {
  font-weight: bolder;

  font-size: 15px;
  margin: 10px 10px;
  font-weight: bolder;
  font-size: 15px;
  margin: 10px 10px;
}

.tag_music {
  margin: 10px 10px;
}
.btn-follow {
  flex-shrink: 0;
  margin-left: auto;
  height: 30px;
  border-radius: 10px;
  padding: 0 20px;
  border: 1px solid red;
  background-color: transparent;
  color: red;
  font-weight: 400;
}

.video_content-wrapper {
  // width:60%;
  display: flex;
  max-width: 500px;
  padding-bottom: 15px;
  border-bottom: 2px solid rgb(207, 200, 200);
  justify-content: center;
}
.video_content-clip {
  // width: 40%;
  max-width: 300px;
  max-height: 400px;
  // max-width: 100%;
  border-radius: 10px;
  object-fit: cover;
  flex-shrink: 0;
  margin: auto;
}

.video_content-emoji {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: center;

  margin-left: 20px;
  span {
    margin: 5px;
    font-weight: bolder;
  }
  img {
    display: none;
  }

  span:nth-child(5) {
    position: relative;

    &:before {
      position: absolute;
      height: 20px;
      width: 20px;
      content: "";
      top: -12px;
      padding: 30px;
      left: 0;
      right: 0;
    }

    &:hover .video_share-wrapper {
      display: block;
    }
    // &:hover &:before {
    //   display: block;
    // }
  }
  position: relative;
}
.icon-sub {
  background-color: red;
}

.video_content-icon {
  width: 32px;
  height: 32px;
  padding: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  text-align: center;
  background-color: rgba(22, 24, 35, 0.06);

  &:hover {
    cursor: pointer;
  }
}
.video-border {
  width: 100%;

  border-bottom: 1px solid rgb(207, 200, 200);
}
.video_share-wrapper {
  display: none;
  width: 300px;

  border-radius: 5px;
  border: 1px solid rgb(207, 200, 200);
  box-shadow: 0 0 10px rgba(22, 24, 35, 0.06);
  z-index: 2;
  position: absolute;
  background-color: white;

  padding: 0;
  transform: translateY(calc((50% + 34px) * (-1)));
  left: 0;
  &:hover {
    display: block;
  }
  // &:after,
  &:before,
  &:after {
    content: "";
    position: absolute;

    bottom: -12px;
    left: 10px;
    width: 20px;
    height: 20px;
    border: 1px solid rgb(207, 200, 200);
    transform: rotate(45deg);
    z-index: -1;
  }
  &:after {
    background-color: white;
    border: 0;
    left: 6px;
    padding: 5px;
    bottom: -9px;
  }
}

.video_share-list {
  list-style: none;
  padding: 10px;
}
.video_share-item {
  display: flex;
  // height:20px;
  align-items: center;
  &:hover {
    background-color: $gray-color;
  }
}
.video_share-item-link {
  padding: 0;
  color: #000;
  font-size: 1.2rem;
  text-decoration: none;
}
.video_share-item-icon {
  font-size: 1.8rem;
  line-height: 20px;
  padding: 6px;
  border-radius: 50%;
  text-align: center;
}
.video_share-item-icon:nth-child(1) {
  color: #555;
}


#toast {
  position: fixed;
  top: 32px;
  right: 32px;
  z-index: 999999;
}

.toast {
  display: flex;
  align-items: center;
  background-color: #fff;
  border-radius: 2px;
  padding: 20px 0;
  min-width: 400px;
  max-width: 450px;
  border-left: 4px solid;
  box-shadow: 0 5px 8px rgba(0, 0, 0, 0.08);
  transition: all linear 0.3s;
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(calc(100% + 32px));
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeOut {
  to {
    opacity: 0;
  }
}

.toast--success {
  border-color: #47d864;
}

.toast--success .toast__icon {
  color: #47d864;
}

.toast--info {
  border-color: #2f86eb;
}

.toast--info .toast__icon {
  color: #2f86eb;
}

.toast--warning {
  border-color: #ffc021;
}

.toast--warning .toast__icon {
  color: #ffc021;
}

.toast--error {
  border-color: #ff623d;
}

.toast--error .toast__icon {
  color: #ff623d;
}

.toast + .toast {
  margin-top: 24px;
}

.toast__icon {
  font-size: 24px;
}

.toast__icon,
.toast__close {
  padding: 0 16px;
}

.toast__body {
  flex-grow: 1;
}

.toast__title {
  font-size: 16px;
  font-weight: 600;
  color: #333;
}

.toast__msg {
  font-size: 14px;
  color: #888;
  margin-top: 6px;
  line-height: 1.5;
}

.toast__close {
  font-size: 20px;
  color: rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

